
import vSearch from '../module/Search'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},

components: {
vSearch,
Previewer,
},

data() {
return {
showData: this.$route.params,
value: '',
itemAllShow: false,
schoolName:'',
tableData:[],
status:localStorage.getItem('status'),
ifCheck:localStorage.getItem('ifCheck'),
options: {
getThumbBoundsFn (index) {
let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]
let pageYScroll = window.pageYOffset || document.documentElement.scrollTop
let rect = thumbnail.getBoundingClientRect()
return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}
}
},
imgList:[],
}
},

mounted() {
this.initSchoolList();

},

methods: {
searchBlur(context) {

console.log(context);
this.schoolName = context;
this.initSchoolList();
},
initSchoolList(){
let temp = this;
this.$post('/web/querySchoolList', {
"projectId": temp.showData.projectId,
"schoolName": temp.schoolName,
"stage": temp.showData.id,
"shenbao":"",
"year":"",
"pageSize": 10000
}).then((m)=> {
if(m.resultCode == 0){
temp.tableData = m.content.page.content;
}
})
},

show (i, index, fileList) {




fileList.forEach((item, index) => {
this.imgList.push({
msrc: item.fileUrl,
src: item.fileUrl,
w: 800,
h: 400
})
})
this.$refs.previewer[index].show(i)
},

itemAllToggle() {
this._index === undefined ? this._index = 0 : this._index++;
this.tableData.forEach(item => {

if( typeof item.isShow === 'undefined' ) {
this.$set(item, 'isShow', false);
}

if( this._index % 2 === 0 ) {
item.isShow = true;
}else {
item.isShow = false;
}
})
},

toggleItem(item) {
if( typeof item.isShow === 'undefined' ) {
this.$set(item, 'isShow', true);
}else {
item.isShow = !item.isShow;
}
}
}
}



