export default {
	data() {
		return {
			searchContext: '',
			maskElt: null,
		}
	},

	props: {
		scrollView: {
			type: String,
			default: 'body'
		},

		placeholder: String,

		hasBorder: {
			type: Boolean,
			default: true
		}
	},
	
	watch: {
		
	},

	mounted() {
		this.$nextTick(() => {
		})
	},

	methods: {
		searchMethod(e) {
			e.preventDefault();
			e.target[0].blur();
		},

		blur() {
			this.$emit('blur', this.searchContext);
			document.getElementsByTagName('body')[0].removeChild(this.maskElt);
		},

		focus(e) {
			this.$emit('focus');
			let sel = this.$refs['z-search'],
					top = sel.clientHeight + sel.offsetTop;
			let mask = document.createElement('div');
			mask.className = 'z-mask-box__search';
			mask.style.top = top + 'px'
			document.body.appendChild(mask);
			this.maskElt = document.getElementsByClassName('z-mask-box__search')[0]
			this.maskElt.addEventListener('touchstart', (event) => {
				event.preventDefault();
				e.target.blur();
			})
		},

	}
}



